import { Component } from "react";
import axios from "axios";

const GET = "http://localhost:8082/getAllPetList";
const savePet = "http://localhost:8082/doPetInsert";
const FetchId = "http://localhost:8082/GetPetId/";
const GetIdList = "http://localhost:8082/getPetIdList";

const fetchByName = "http://localhost:8082/findByPetName/{petName}";

class PetService extends Component {
    getPets() {
        return axios.get(GET);
      }
      AddPet(pet) {
        return axios.post(savePet,pet);
      }
      fetchPetById(petId) {
        return axios.get(FetchId + petId);
      }
     
      fetchByName(petName) {
        return axios.get(fetchByName + petName);
      }                              
     
      getAllIdList() {
        return axios.get(GetIdList);
      }
}
export default new PetService()
